<template>
  <div class="merchantOrders">
    <div class="merchantOrdersHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.push('/merchantCenter')" />
        <span>{{ $t("index.text81") }}</span>
      </div>
    </div>
    <div class="slot"></div>

    <div class="merchantOrdersMain">
      <van-tabs
        @click="changeTab"
        v-model="status"
        line-width="2rem"
        background="#fffbf1"
      >
        <van-tab :title="$t('index.text111')" :name="0">
          <OrderList :type="1" :orderInfoList="orderInfoList"></OrderList>
        </van-tab>
        <van-tab :title="$t('index.text112')" :name="1">
          <OrderList :type="1" :orderInfoList="orderInfoList"></OrderList>
        </van-tab>
        <van-tab :title="$t('index.text113')" :name="4">
          <OrderList :type="1" :orderInfoList="orderInfoList"></OrderList
        ></van-tab>
        <van-tab :title="$t('index.text114')" :name="2">
          <OrderList :type="1" :orderInfoList="orderInfoList"></OrderList
        ></van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import OrderList from "./components/orderList.vue";
export default {
  components: {
    OrderList
  },
  data() {
    return {
      status: 0,
      orderInfoList: []
    };
  },
  async activated() {
    if (this.status == 4) {
      this.getEvaluateList();
    } else {
      this.getList(this.status);
    }
  },
  methods: {
    getList(v) {
      this.$api
        .get("orders/status", {
          status: v,
          type: 1
        })
        .then(res => {
          this.orderInfoList = res.data;
        });
    },

    // 未评价列表
    getEvaluateList() {
      this.$api.get("orders/evaluate", {}).then(res => {
        this.orderInfoList = res.data;
      });
    },

    changeTab(v) {
      if (v != 4) {
        this.getList(v);
      } else {
        this.getEvaluateList();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.merchantOrders {
  height: 100%;
  overflow: scroll;
  background: #fffbf1;
}
.merchantOrdersHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}
.merchantOrdersMainHeader {
  background: #bd303e;
  height: 37.5rem;
}
.merchantOrdersMain {
  padding: 0 1.6rem 1.6rem;
}
</style>



