<template>
  <div class="orderList">
    <div class="orderItem" v-for="item in orderInfoList" :key="item.id">
      <div class="orderNum">{{ $t("index.text115") }}{{ item.orderNo }}</div>
      <div class="orderContent">
        <img :src="$url + item.image" class="left" alt="" />
        <div class="right">
          <div class="textInfo">
            {{ item.title }}
          </div>

          <div class="price">{{ item.price }}HF</div>
        </div>
      </div>

      <div class="line"></div>

      <div class="btnBox jcsb mt2r" v-if="item.status == 0">
        <div class="left">{{ $t("index.text111") }}</div>
        <div class="right jcc" @click="getOrderDetails(item.id)">
          {{ $t("index.text116") }}
        </div>
      </div>

      <div class="btnBox jcsb mt2r" v-if="item.status == 1">
        <div class="left">{{ $t("index.text112") }}</div>
        <div class="right jcc" @click="getOrderDetails(item.id)">
          {{ $t("index.text116") }}
        </div>
      </div>

      <div
        class="btnBox jcsb mt2r"
        v-if="item.status == 2 && item.is_evaluate == 0"
      >
        <div class="left">{{ $t("index.text113") }}</div>

        <div class="right jcc" @click="getOrderDetails(item.id)">
          {{ $t("index.text116") }}
        </div>
      </div>

      <div
        class="btnBox jcsb mt2r"
        v-if="item.status == 2 && item.is_evaluate == 1"
      >
        <div class="left">{{ $t("index.text114") }}</div>
        <div class="right jcc" @click="getOrderDetails(item.id)">
          {{ $t("index.text116") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    orderInfoList: {
      type: Array,
      default: () => []
    },

    type: {
      type: Number,
      default: 0
    }
  },

  data() {
    return {};
  },
  methods: {
    getOrderDetails(id) {
      this.$router.push({
        path: "/orderDetails",
        query: {
          orderId: id,
          type: this.type
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.orderList {
  .orderItem {
    margin-top: 1.4rem;
    padding: 1.4rem;
    background: #fff;
    .orderNum {
      font-size: 1.2rem;
    }

    .orderContent {
      margin: 1.4rem 0;
      padding: 1rem;
      background: #f7f7f7;
      display: flex;
      .left {
        width: 8.5rem;
        height: 8.5rem;
      }

      .right {
        margin-left: 2rem;
        flex: 1;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .textInfo {
          font-weight: bold;
          font-size: 1.2rem;
          line-height: 2.5rem;
        }
        .price {
          font-size: 1.8rem;
          color: #d12c21;
          font-weight: bold;
        }
      }
    }

    .line {
      background: #cccccc;
      height: 0.1rem;
    }

    .btnBox {
      .left {
        color: #d12c21;
        font-weight: bold;
      }
      .right {
        padding: 1rem 1.5rem;
        height: 3rem;
        background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
        border-radius: 1.5rem;
        color: #fff;
      }
    }
  }
}
</style>



